import React, { Component } from 'react';
import { useQuery } from '@apollo/client';
import styled from '@emotion/styled';
import { GET_POKEMON } from '../graphql/get-pokemon';
import { RemoveButton } from './FunctionClick';


export class Get extends Component {
    constructor(props) {
        super(props);
    }
    render(){
        const getname = this.props.Name;
        

        return (
            <div>
                <MyPokeComponent getname={getname} />
            </div>
        )
    
    }
}

export function MyPokeComponent (Bebek) {
    const Nickname = Bebek.getname;
    const pokemonName = localStorage.getItem(Nickname);

    const { 
    loading,
    error, 
    data } = useQuery(GET_POKEMON, {
    variables: {name: pokemonName},
    });

    if (loading) return 'Loading...';
    if (error) return `Error! ${error.message} ${pokemonName}`;

    return (
        <Container>
            <Back>
                <Pic 
                    src={data.pokemon.sprites.front_default}
                    alt={data.pokemon.name}
                />
                
                <RemoveButton Nickname={Nickname}/>

                {/* <Catch>
                    Remove
                </Catch> */}

            </Back>
        </Container>
    );
};


const Container = styled.div`
    display: flex;
    flex-wrap: wrap;  
    justify-content: center;
`;

const Pic = styled.img``;

const Back = styled.div``;

const Catch = styled.button`
    padding: 0.8rem;
    margin-bottom: 3rem;
    cursor: pointer;
    border-radius: 15px;
    border: none;
    outline: none;
    font-weight: 800;
    text-transform:uppercase;
    &: hover {
    background-color: #CC2020;
    color: #F3F3F3;
    }
`;